.chat-overlay {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  background-color: #ffffff;
  overflow: hidden;
}

.chat-header {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  background-color: #f5f5f5;
  border-bottom: 1px solid #ddd;
  justify-content: space-between;
}

.chat-header-elements {
  display: flex;
  align-items: center;
  gap: 8px;
}

.chat-header-info {
  margin-left: 12px;
}

.chat-header-name {
  margin: 0;
  font-size: 16px;
  font-weight: bold;
}

.chat-footer {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background-color: #f5f5f5;
  border-top: 1px solid #ddd;
}

.chat-footer input {
  flex-grow: 1;
  margin-right: 8px;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ddd;
}

.chat-send-button {
  padding: 8px 16px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.chat-send-button:hover {
  background-color: #0056b3;
}

.chat-empty-state {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

.trash-icon:hover {
  color: red;
}
